<template>
  <div class="bigcontain">
    <div class="contain">
      <div class="WelcomeSignUp fluidityWelcomeASignUp">
        <div class="cat">
          <span><img src="../assets/New2.png" alt="" /></span>
        </div>
        <WelcomeHello
          v-if="stabled"
          :NewWidth="NewWidth"
          :TypewriterCase="typewriterActive"
        />
      </div>
      <div class="SignUp fluidityLoginASignUp">
        <div class="divform">
          <form class="form" @submit.prevent="registerUser">
            <div class="NameBox">
              <label for="FirstName" class="FirstName">
                <input
                  v-model="user.FirstName"
                  type="text"
                  name="FirstName"
                  placeholder
                  id="FirstName"
                />
                <span>First Name</span>
                <font-awesome-icon class="icon" icon="fa-solid fa-user" />
                <div class="bord"></div>
              </label>
              <label for="Surname" class="Surname">
                <input
                  v-model="user.Surname"
                  type="text"
                  name="Surname"
                  placeholder
                  id="Surname"
                />
                <span>Surname</span>
                <font-awesome-icon class="icon" icon="fa-solid fa-user" />
                <div class="bord"></div>
              </label>
            </div>
            <div class="MobilenumberBox">
              <label for="Mobilenumber" class="Mobilenumber">
                <input
                  v-model="user.Mobilenumber"
                  type="number"
                  name="Mobilenumber"
                  placeholder
                  id="Mobilenumber"
                />
                <span>Mobile Number</span>
                <font-awesome-icon class="icon" icon="fa-solid fa-phone" />
                <div class="bord"></div>
              </label>
            </div>
            <div class="EmailBox">
              <label for="Email" class="Email">
                <input
                  v-model="user.email"
                  type="text"
                  name="Email"
                  placeholder
                  id="Email"
                />
                <span>Email</span>
                <font-awesome-icon class="icon" icon="fa-solid fa-envelope" />
                <div class="bord"></div>
              </label>
            </div>
            <div class="PasswordBox">
              <label for="Password" class="Password">
                <input
                  v-model="user.Password"
                  type="Password"
                  name="Password"
                  placeholder
                  id="Password"
                />
                <span>Password</span>
                <font-awesome-icon class="icon" icon="fa-solid fa-lock" />
                <div class="bord"></div>
              </label>
            </div>
            <div class="DateBox">
              <label for="day" class="dayLabel">
                <input
                  id="day"
                  class="X"
                  type="text"
                  v-model="user.day"
                  placeholder
                />

                <div class="bord"></div>
                <ul class="day">
                  <li v-for="day in 30" @click="user.day = day" :key="day">
                    {{ day }}
                  </li>
                </ul>
                <span>Day</span>
              </label>
              <label for="month" class="monthIabel">
                <input
                  id="month"
                  type="text"
                  v-model="user.month"
                  placeholder
                />
                <div class="bord"></div>
                <ul>
                  <li
                    v-for="month in ArrMonth"
                    @click="user.month = month"
                    :key="month"
                  >
                    {{ month }}
                  </li>
                </ul>
                <span>month</span>
              </label>
              <label for="year" class="yearIabel">
                <input id="year" type="text" v-model="user.year" placeholder />

                <div class="bord"></div>
                <ul class="year">
                  <li
                    @click="user.year = year"
                    v-for="year in ArrYears"
                    :key="year"
                    id="year"
                  >
                    {{ year }}
                  </li>
                  <!-- <RecycleScroller
                    class="scroller"
                    :items="ArrYears"
                    :item-size="10"
                    key-field="year"
                    v-slot="{ item }"
                  >
                    <li @click="user.year = item">{{ item }} s</li>
                  </RecycleScroller> -->
                </ul>
                <span>year</span>
              </label>
            </div>
            <div class="genderBox">
              <label for="gender" class="genderIabel">
                <div class="gender">
                  {{ user.gender }}
                  <font-awesome-icon
                    v-if="user.gender == ''"
                    icon="fa-solid fa-venus-mars"
                    class="icon"
                  />
                  <font-awesome-icon
                    v-if="user.gender == 'Male'"
                    icon="fa-solid fa-mars"
                    class="icon"
                  />
                  <font-awesome-icon
                    v-if="user.gender == 'Female'"
                    icon="fa-solid fa-venus"
                    class="icon"
                  />
                </div>

                <ul>
                  <li @click="(user.gender = 'Male'), SelectGender()">
                    Male
                    <font-awesome-icon icon="fa-solid fa-mars" />
                  </li>
                  <li @click="(user.gender = 'Female'), SelectGender()">
                    Female
                    <font-awesome-icon icon="fa-solid fa-venus" />
                  </li>
                </ul>
              </label>
              <div class="bord"></div>
              <span>Gender</span>
            </div>
            <button class="SignUpbutton">Sign Up</button>
            <hr />
            <div class="Login" @click="Login()">Login?</div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import firebase from "../firebase/init";
import {
  getAuth,
  createUserWithEmailAndPassword,
  onAuthStateChanged,
} from "firebase/auth";
import { doc, setDoc } from "firebase/firestore";
import { db } from "../firebase/init";
import router from "../router";
import WelcomeHello from "../components/Welcome.vue";
export default {
  name: "SignUp",
  components: {
    WelcomeHello,
  },
  data() {
    return {
      ArrMonth: [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ],
      user: {
        FirstName: "",
        Surname: "",
        Mobilenumber: "",
        email: "",
        Password: "",
        uid: "",
        month: "",
        day: "",
        year: "",
        gender: "",
        ProfileImage: "",
      },
      typewriterActive: true,
      NewWidth: "",
      stabled: true,
      firebase,
      ArrYears: Array.from(
        { length: 2013 - 1940 + 1 },
        (_, index) => 1940 + index
      ).reverse(),
    };
  },
  methods: {
    SelectGender() {
      document.querySelector(".genderBox span").style.top = "-16px";
      document.querySelector(".genderBox .bord").style.width = "100%";
    },
    Login() {
      this.typewriterActive = false;
      const divLogin =
        document.getElementsByClassName("WelcomeSignUp")[0].classList;
      divLogin.remove("fluidityWelcomeASignUp");
      divLogin.add("fluidityWelcomeBSignUp");
      const divWelcome = document.getElementsByClassName("SignUp")[0].classList;
      divWelcome.remove("fluidityWelcomeASignUp");
      divWelcome.add("fluidityLoginOutBSignUp");
      setTimeout(() => {
        router.push("Login");
      }, 400);
    },
    registerUser() {
      createUserWithEmailAndPassword(
        getAuth(),
        this.user.email,
        this.user.Password
      ).then((dataUser) => {
        try {
          setDoc(doc(db, "users", dataUser.user.uid), {
            FirstName: this.user.FirstName,
            Surname: this.user.Surname,
            Mobilenumber: this.user.Mobilenumber,
            email: this.user.email,
            Password: this.user.Password,
            uid: dataUser.user.uid,
            month: this.user.month,
            day: this.user.day,
            year: this.user.year,
            gender: this.user.gender,
            coverImage: "",
            ProfileImage:
              "data:image/jpeg;base64,",
          });
        } catch (error) {
          console.error("Error adding document: ", error);
        }

        router.push({
          name: "AllPosts",
        });
      });
    },
    resizeing() {
      if (window.innerWidth <= 430) {
        //
        this.NewWidth = "X";
      }
      if (window.innerWidth > 430 && window.innerWidth <= 850) {
        //
        this.NewWidth = "XX";
      }
      if (window.innerWidth > 850) {
        //
        this.NewWidth = "XXX";
      }
    },
    chek() {
      this.$watch("NewWidth", () => {
        this.stabled = false;
        setTimeout(() => {
          this.stabled = true;
        }, 10);
      });
    },
    start() {
      const auth = getAuth();
      onAuthStateChanged(auth, async (user) => {
        if (user) {
          router.push({
            name: "AllPosts",
          });
        } else {
          router.push({
            name: "SignUp",
          });
        }
      });
    },
  },
  created() {
    this.resizeing();
    this.chek();
    this.start();
  },
  mounted() {
    window.addEventListener("resize", this.resizeing);
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.resizeing);
  },
};
</script>
<style scoped>
